.container {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    height: 100vh;
    width: 100vw;
    align-items: center;
    justify-content: center;
    color: black;
    font-family: 'Roboto',sans-serif;
}

.profile-icon {
    position: relative;
    top: 5px;
    left: 0px;
    bottom: 0px;
    color: darkblue;
    margin-right: 8px;
  }

.email {
    text-align: right;
}

.summary {
    order:0;
}

.profile-picture{
    order:1;
}

.employment {
    order:2;
}

.technical-skills {
    order:3;
}

.education {
    order:4;
}

.personal-skills {
    order:5
}

@media only screen and (max-width: 900px){
    .email{
        text-align:left
    }

    .profile-picture{
        order:0;
    }
    
    .summary {
        order:1;
    }
    
    .employment {
        order:2;
    }
    
    .education {
        order:3;
    }

    .technical-skills {
        order:4;
    }
    
    .personal-skills {
        order:5
    }

}